import React, { Component } from 'react';
import {Header, Listing } from '../components'

export default class Destination extends Component {

	constructor() {
		super()
		this.state = {
		  destination: null, 
		}
	}

	getDestination() {
		let self = this
		fetch('https://hotelguia.herokuapp.com/destinations')  
		  .then(function(response) {
		    response.json().then(function(json) {
		      	console.log(json)
		      	var found = json.filter(function(item) { 
		      		console.log(item)
		      		console.log(self.props.slug)
		        	return (item.slug === self.props.slug)
		      	});
		     	console.log(found)

		      	if (found.length > 0) {
		        	self.setState({
		          		destination : found[0]
		        	})
		      	}
		    })
		})
	}

	componentDidMount() {
		this.getDestination()
	}

	render() {
		let destination = this.state.destination
		if(!destination) {
			return (null)
		}

		return(
			<div>
			  <Header src={ destination.image } title={ destination.name } headerFixed={true} />
			  <Listing destination={destination} />
			</div>
		)
	}
}



